import Team from './Team'
/**
 *      allowedTypes 
 *    1  maxLevel
 *
 * @param allowedTypes  
 * @param maxLevel    
 * @returns ,    
 *     
 *
 */
export function * characterGenerator (allowedTypes, maxLevel) {
  while (true) {
    yield new allowedTypes[Math.floor(Math.random() * allowedTypes.length)](
      Math.floor(Math.random() * maxLevel + 1)
    )
  }
}

/**
 *      characterGenerator
 * @param allowedTypes  
 * @param maxLevel    
 * @param characterCount  ,   
 * @returns  Team,   .     - characterCount
 * */
export function generateTeam (allowedTypes, maxLevel, characterCount) {
  const heroesList = []
  const heroGenerator = characterGenerator(allowedTypes, maxLevel)
  for (let i = 0; i < characterCount; i += 1) {
    heroesList.push(heroGenerator.next().value)
  }
  return new Team(heroesList)
}
