const axios = require("axios");

export async function createUserApi(user = "test") {
  try {
    const response = await axios.post("http://localhost:8000/test/", {
      tg_name: user,
    });
  } catch (error) {
    console.error("Error creating :", error.message);
  }
}

export async function getDataApi(user = "test") {
  try {
    const response = await axios
      .get("http://localhost:8000/test/" + user + "/")
      .then((result) => result.data);

    return response;
  } catch (error) {
    console.error("Error creating :", error.message);
  }
}

export async function updateDataApi(user = "test", data) {
  try {
    const response = await axios.patch(
      "http://localhost:8000/test/" + user + "/",
      {
        data,
      }
    );
    return response.data;
  } catch (error) {
    console.error("Error creating :", error.message);
  }
}
